.alerts {
    content: ' ';
}

.alerts .alert {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/ - (30px - 30px * 1/));
}

.alerts .alert:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}

.alerts .alert:last-child {
    margin-right: 0;
}

.alerts .alert:nth-child( '' ''n) {
    margin-right: 0;
    margin-left: auto;
}

.alert {
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 30px;
    font-family: 'Open Sans';
    position: relative;
    color: $white;
}

.alert a {
    color: $white;
}

.alert.warning {
    background: #ffc32b;
    color: #5f4b28;
}

.alert.warning a {
    color: #5f4b28;
}

.alert.warning .close {
    color: #8c6b17;
}

.alert.warning .button {
    background: #bf8d27;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: #bf8d27;
}

.alert.warning .button:hover {
    box-shadow: 0 0 0 4px #bf8d27,
            inset 0 0 0 3px $white;
    color: #fff;
}

.alert.error {
    background: #cc2b3f;
}

.alert.error .close {
    color: #701722;
}

.alert.error .button {
    background: #9d1525;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: #9d1525;
}

.alert.error .button:hover {
    box-shadow: 0 0 0 4px #9d1525,
            inset 0 0 0 3px $white;
    color: #fff;
}

.alert.success {
    background: #79a944;
}

.alert.success .close {
    color: #415c25;
}

.alert.success .button {
    background: #618738;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: #618738;
}

.alert.success .button:hover {
    box-shadow: 0 0 0 4px #618738,
            inset 0 0 0 3px $white;
    color: #fff;
}

.alert.neutral {
    background: #559cbe;
}

.alert.neutral .close {
    color: #2e5568;
}

.alert.neutral .button {
    background: #3d748f;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: #3d748f;
}

.alert.neutral .button:hover {
    box-shadow: 0 0 0 4px #3d748f,
            inset 0 0 0 3px $white;
    color: #fff;
}

.alert.small .title,
        .alert.small .body {
    display: inline-block;
}

.alert .title {
    font-weight: bold;
}

.alert .button {
    border-radius: 5px;
    margin-right: 20px;
    color: $white !important;
}

.alert a {
    font-weight: bold;
}

.alert .close {
    text-decoration: none;
}

.alert .secondary-action {
    font-weight: normal;
}

.alert .close {
    position: absolute;
    top: 20px;
    right: 20px;
    color: $gray_dark2;
    text-shadow: 1px 1px 0 $gray_light2;
    font-size: 16px;
}
